<template>
  <div id="app" class="container">
    <div class="row" v-bind:class="{ 'visible': !room, 'hidden': room }">
      <NameInput buttonText="Join" @submit="joinSession" />
    </div>
    <div
      class="row"
      v-bind:class="{ 'visible': room && room.game.screen === 'LOBBY', 'hidden': !room || room.game.screen !== 'LOBBY' }"
    >
      <Lobby
        :socket="socket"
        :room="room"
        :players="players"
        :isPlayerReady="isPlayerReady"
        @percival-enabled="percivalEnabled"
        @oberon-enabled="oberonEnabled"
        @morgana-enabled="morganaEnabled"
        @player-ready="readyUp"
        @player-not-ready="notReady"
      />
    </div>

    <div
      class="row"
      v-bind:class="{ 'visible': room && room.game.screen === 'ROLE_REVEAL', 'hidden': !room || room.game.screen !== 'ROLE_REVEAL' }"
    >
      <Reveal
        :players="players"
        :isPlayerReady="isPlayerReady"
        :team="team"
        :role="role"
        :settings="room && room.game ? room.game.settings : {}"
        :metadata="metadata"
        @player-ready="readyUp"
        @player-not-ready="notReady"
      />
    </div>

    <div
      class="row"
      v-bind:class="{ 'visible': room && room.game.screen === 'GAME', 'hidden': !room || room.game.screen !== 'GAME' }"
    >
      <Game
        :game="room && room.game ? room.game : {}"
        :players="players"
        :team="team"
        :role="role"
        :playerId="getPlayerId()"
        :isPlayerReady="isPlayerReady"
        @reveal-quest-result="revealQuestResult"
        @propose-player-for-quest="proposePlayerForQuest"
        @unpropose-player-for-quest="unproposePlayerForQuest"
        @player-ready="readyUp"
        @player-not-ready="notReady"
      />
    </div>

    <div style="padding-bottom:200px"></div>
  </div>
</template>

<script>
import io from "socket.io-client";
import NameInput from "@/components/avalon/NameInput.vue";
import Lobby from "@/components/avalon/Lobby.vue";
import Reveal from "@/components/avalon/Reveal.vue";
import Game from "@/components/avalon/Game.vue";

export default {
  name: "AvalonGame",
  components: { NameInput, Lobby, Reveal, Game },
  props: {
    socket: {
      type: Object,
      default: function() {
        let socket = null;
        if (process.env.NODE_ENV == "development") {
          socket = io.connect("http://localhost:3000", {
            upgrade: false,
            transports: ["websocket"]
          });
        } else {
          socket = io.connect({ upgrade: false, transports: ["websocket"] });
        }
        return socket;
      }
    },
    roomId: String
  },
  data: function() {
    return {
      room: null,
      players: [],
      team: null,
      role: null,
      metadata: []
      //   room: {
      //     game: {
      //       state: "QUEST_PROPOSING",
      //       screen: "GAME",
      //       settings: {
      //         percivalEnabled: false,
      //         oberonEnabled: false,
      //         morganaEnabled: false
      //       },
      //       currentQuest: {
      //         id: 2,
      //         organiserId: "111",
      //         disagreements: 0,
      //         requiredPlayers: 2,
      //         proposedPlayerIds: ["555", "444"],
      //         proposalAccepted: false,
      //         votes: [
      //           { choice: "SUCCEED", revealed: true },
      //           { choice: "SABOTAGE", revealed: true },
      //           { choice: "SUCCEED", revealed: true }
      //         ],
      //         result: "SUCCEED"
      //       },
      //       questLogs: [
      //         {
      //           id: 1,
      //           requiredPlayers: 2,
      //           playerIds: ["222", "333"],
      //           organiserId: "222",
      //           result: "SUCCEED"
      //         },
      //         {
      //           id: 2,
      //           requiredPlayers: 2,
      //           playerIds: [],
      //           organiserId: "",
      //           result: ""
      //         },
      //         {
      //           id: 3,
      //           requiredPlayers: 4,
      //           playerIds: [],
      //           organiserId: "",
      //           result: ""
      //         },
      //         {
      //           id: 4,
      //           requiredPlayers: 3,
      //           playerIds: [],
      //           organiserId: "",
      //           result: ""
      //         },
      //         {
      //           id: 5,
      //           requiredPlayers: 4,
      //           playerIds: [],
      //           organiserId: "",
      //           result: ""
      //         }
      //       ]
      //     }
      //   },
      //   players: [
      //     { name: "Ben", id: "111", ready: true, vote: "APPROVE", team: "EVIL" },
      //     { name: "Sidd", id: "222", ready: true, vote: "APPROVE", team: "EVIL" },
      //     {
      //       name: "Adam <3",
      //       id: "333",
      //       ready: false,
      //       vote: "REJECT",
      //       team: "EVIL",
      //       role: "ASSASSIN"
      //     },
      //     { name: "Sam", id: "444", ready: false, vote: "REJECT", team: "GOOD" },
      //     {
      //       name: "Rodney",
      //       id: "555",
      //       ready: false,
      //       vote: "REJECT",
      //       team: "GOOD"
      //     },
      //     { name: "Jim", id: "666", ready: false, vote: "REJECT", team: "GOOD" },
      //     { name: "Max", id: "777", ready: false, vote: "REJECT", team: "GOOD" }
      //   ],
      //   team: "EVIL",
      //   role: "ASSASSIN",
      //   metadata: ["111", "222"]
    };
  },
  computed: {
    isPlayerReady: function() {
      let player = this.players.find(o => o.id == this.getPlayerId());
      return player && player.ready;
    }
  },
  created() {
    this.socket.on("players-updated", players => {
      console.log(players);
      this.players = players;
    });
    this.socket.on("player-updated", player => {
      console.log(player);
      let playerToUpdate = this.players.find(o => o.id == player.id);
      for (let k in playerToUpdate) {
        playerToUpdate[k] = player[k];
      }
    });
    this.socket.on("player-assigned", player => {
      console.log(player);
      this.team = player.team;
      this.role = player.role;
      this.metadata = player.metadata.slice();
    });
    this.socket.on("room-updated", room => {
      console.log(room);
      this.room = room;
    });
  },
  methods: {
    percivalEnabled: function(enabled) {
      this.socket.emit("room-updated", {
        game: { settings: { percivalEnabled: enabled } }
      });
    },
    getPlayerId: function() {
      // return "111";
      return this.socket.id;
    },
    morganaEnabled: function(enabled) {
      this.socket.emit("room-updated", {
        game: { settings: { morganaEnabled: enabled } }
      });
    },
    oberonEnabled: function(enabled) {
      this.socket.emit("room-updated", {
        game: { settings: { oberonEnabled: enabled } }
      });
    },
    proposePlayerForQuest: function(playerId) {
      this.socket.emit("room-updated", {
        game: { currentQuest: { playerIdToPropose: playerId } }
      });
    },
    unproposePlayerForQuest: function(playerId) {
      this.socket.emit("room-updated", {
        game: { currentQuest: { playerIdToUnpropose: playerId } }
      });
    },
    joinSession: function(name) {
      this.name = name;
      this.socket.emit("player-joined", {
        name: this.name,
        roomId: this.roomId
      });
    },
    readyUp: function() {
      this.socket.emit("player-updated", { ready: true });
    },
    readyUpWithVote(vote) {
      this.socket.emit("player-updated", { ready: true, vote: vote });
    },
    notReady: function() {
      this.socket.emit("player-updated", { ready: false });
    },
    revealQuestResult: function(id) {
      console.log(id);
    }
  }
};
</script>
<style scoped>
.hidden {
  display: none;
}
